import React from 'react';
import {HashRouter, Route, Switch} from "react-router-dom";
import ImgGallery from './ImgGallery';
// import Response from './Response';

const renderPage = () =>{
    const Header = {letterSpacing: '1px', textTransform: 'uppercase'};
    const sandbox = {margin: '0 auto', width: '50%', transition: 'all 1s ease'};
    return (
        <div  style={sandbox}>
            <h2 style={Header}>Code: 000</h2>
            <h3>Nothing Here</h3>
        </div>
    )
};

const App = ( ) => {

    return (
        <HashRouter>
            <React.Fragment>
                <Switch>
                    <Route path="/" render={renderPage} exact />
                    <Route path="/:Id" component={ImgGallery} exact />
                    <Route render={renderPage} />
                </Switch>
            </React.Fragment>
        </HashRouter>
    )
};
export default App;